<template lang="html">
  <x-header>
    注册
  </x-header>
  <div class="weui_cells weui_cells_form">
    <div class="weui_cell">
      <div class="weui_cell_hd" style="width:5%;">
        <label class="weui_label">
          <i class="fa fa-mobile"></i>
        </label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="text" placeholder="手机号码" v-model="mobile" />
      </div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd" style="width:5%;">
        <label class="weui_label">
          <i class="fa fa-lock"></i>
        </label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="password" placeholder="密码" v-model="password" />
      </div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd" style="width:5%;">
        <label class="weui_label">
          <i class="fa fa-user"></i>
        </label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="text" placeholder="名字" v-model="name" />
      </div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd" style="width:5%;">
        <label class="weui_label">
          <i class="fa fa-h-square"></i>
        </label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="text" placeholder="身高" v-model="height" />
      </div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd" style="width:5%;">
        <label class="weui_label">
          <i class="fa fa-wordpress"></i>
        </label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="text" placeholder="体重" v-model="weight" />
      </div>
    </div>
  </div>
  <div class="button_sp_area" style="margin-top:30px;padding: 10px;">
    <a @click="signUp" href="javascript:;" class="weui_btn weui_btn_plain_primary">注 册</a>
  </div>
  <divider>已有账号?</divider>
  <div class="button_sp_area" style="margin-top:5px;padding: 10px;">
    <a v-link="{ name: 'signin' }" class="weui_btn weui_btn_plain_primary">登 录</a>
  </div>
  <toast :show.sync="regFail" type="text">注册失败</toast>
</template>

<script>
  import XHeader from 'vux/dist/components/x-header'
  import Divider from 'vux/dist/components/divider'
  import Toast from 'vux/dist/components/toast'

  export default {
    data: function () {
      return {
        mobile: '',
        password: '',
        name: '',
        height: 0,
        weight: 0,
        regFail: false
      }
    },
    computed: {},
    ready: function () {},
    attached: function () {},
    methods: {
      signUp () {
        if (this.mobile === '' && this.password === '' && this.name === '') {
          this.$set('regFail', true)
          return
        }
        let user = {
          mobile: this.mobile,
          password: this.password,
          name: this.name,
          height: this.height,
          weight: this.weight
        }
        this.$http.post(this.$config.API + '/users.json', {user: user}).then(function (response) {
          // 注册成功
          this.$route.router.go({name: 'signin'})
        }, function (response) {
          // 注册失败
          this.$set('regFail', true)
        })
      }
    },
    components: {
      XHeader,
      Divider,
      Toast
    }
  }
</script>

<style lang="scss">
</style>



